* {
  font-family: Roboto, sans-serif;
}

html {
  --main-color: #00acdc;
  --header-height: 60px;
}

body {
  background-color: #787575;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logoImg {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  height: var(--header-height);
  align-items: center;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__items {
  height: 100%;
  margin-right: 20px;
}

.nav__items:last-child {
  margin: 0;
}

.nav__links {
  line-height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  white-space: nowrap;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.nav__links:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

